
import Link from 'next/link';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import type { TestType } from '@/types';
import { cn } from '@/lib/utils';
import { useAuth } from '@/hooks/useAuth';
import { Lock } from 'lucide-react';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from './ui/tooltip';

interface TestCardProps {
  testId: TestType;
  title: string;
  description: string;
  Icon: React.ElementType;
  iconBg: string;
  isFormula?: boolean;
}

export function TestCard({ testId, title, description, Icon, iconBg, isFormula = false }: TestCardProps) {
  const { profile } = useAuth();
  const isPro = profile?.subscriptionStatus === 'pro';

  const renderLink = (difficulty: 'easy' | 'medium' | 'hard', children: React.ReactNode) => {
    if (isPro) {
      return <Link href={`/tests/${testId}/${difficulty}`}>{children}</Link>;
    }
    return <div className="w-full h-full flex items-center justify-center">{children}</div>;
  };

  const buttonContent = (difficulty: string) => (
      <div className="flex items-center justify-center">
        {!isPro && <Lock className="w-4 h-4 mr-2" />}
        {difficulty}
      </div>
  );
  
  const formulaButtonContent = () => (
       <div className="flex items-center justify-center">
        {!isPro && <Lock className="w-4 h-4 mr-2" />}
        Practice
      </div>
  );

  return (
    <Card className="flex flex-col shadow-lg hover:shadow-2xl hover:-translate-y-2 transition-all duration-300">
      <CardHeader className="flex-col items-center text-center">
        <div className={cn("p-4 rounded-full mb-4 w-fit", iconBg)}>
          <Icon className="w-10 h-10 text-primary" />
        </div>
        <CardTitle>{title}</CardTitle>
      </CardHeader>
      <CardContent className="flex-grow text-center">
        <CardDescription>{description}</CardDescription>
      </CardContent>
      <CardFooter>
        <TooltipProvider>
          {isFormula ? (
             <div className="w-full flex flex-col sm:flex-row gap-2">
                <Tooltip>
                    <TooltipTrigger asChild>
                        <Button asChild className="flex-1" variant="outline" disabled={!isPro}>
                            {renderLink('easy', formulaButtonContent())}
                        </Button>
                    </TooltipTrigger>
                    {!isPro && <TooltipContent><p>Upgrade to Pro to access this test.</p></TooltipContent>}
                </Tooltip>
             </div>
          ) : (
            <div className="w-full flex flex-col sm:flex-row gap-2">
              <Tooltip>
                <TooltipTrigger asChild>
                  <Button asChild className="flex-1" variant="outline" disabled={!isPro}>
                     {renderLink('easy', buttonContent('Easy'))}
                  </Button>
                </TooltipTrigger>
                {!isPro && <TooltipContent><p>Upgrade to Pro to access this test.</p></TooltipContent>}
              </Tooltip>
              <Tooltip>
                <TooltipTrigger asChild>
                   <Button asChild className="flex-1" variant="secondary" disabled={!isPro}>
                      {renderLink('medium', buttonContent('Medium'))}
                   </Button>
                </TooltipTrigger>
                {!isPro && <TooltipContent><p>Upgrade to Pro to access this test.</p></TooltipContent>}
              </Tooltip>
              <Tooltip>
                <TooltipTrigger asChild>
                  <Button asChild className="flex-1" disabled={!isPro}>
                      {renderLink('hard', buttonContent('Hard'))}
                  </Button>
                </TooltipTrigger>
                {!isPro && <TooltipContent><p>Upgrade to Pro to access this test.</p></TooltipContent>}
              </Tooltip>
            </div>
          )}
        </TooltipProvider>
      </CardFooter>
    </Card>
  );
}
